.wrapper--register{
    color:$dark75;
    margin: 0 24px;
    
}
.link--home{
    padding:32px 0;
    color:$dark50;

}
.register{
    display: grid;
    &__input{
        border: 1px solid $dark5;
        height: 48px;
        border-radius: 5px;
        margin:4px 0 16px 0;
        padding: 0 12px;
        font:$input-placeholder;
        color:$dark;
    }

    &__input:focus{
        border: 1px solid $green;
        outline: none;
    }
        &__label{
            font:$input-placeholder;
            color:$dark50;
        }
        
    

    &__message-password{
        font-size: 14px;
        color: $dark50;
    }
}