$body-text : 16px 'Muli', sans-serif;
$subtitle: 12px 'Muli', sans-serif;
$button-font: 700 14px 'Muli', sans-serif;
$input-placeholder: 14px 'Muli', sans-serif;

$purple: #3F2B96;
$green: #50B895;
$blue: #66A2F0;
$red: #F6736E;
$dark: #1F2041;
$dark75:#575870;
$dark50: #8F8FA0;
$dark25: #C7C7CF;
$dark5: #E9E9EC;
$white: #ffffff;

@mixin shadow{
    box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.05);
}

@mixin center{
    text-align:center;
}

body{
    margin:0;
    font: $body-text;
    
}
.body--white{
    background-color: $white;
}

.body--purple{
    color: $white;
    background-color: $purple;
}

.validation-summary-errors{
    color:$red;
    font:$input-placeholder;
    ul{
        padding-inline-start: 0;
        li{
            list-style: none;
            
        }
    }
   
}